import { useEffect, useState, useContext } from "react";
import axios from "axios";
import Event from "./Event.jsx";
import Context from "../../contexts/Context";
import ModalCreacteEvent from "./ModalCreateEvent.jsx";
import ModalUpdateEvent from "./ModalUpdateEvent.jsx";

const Events = ({ setShowModal }) => {
  const { cookies, showModalUpdateEvent, events, setEvents } =
    useContext(Context);
  const [showModalCreateEvent, setShowModalCreateEvent] = useState(false);
  const [nextEvents, setNextEvents] = useState([]);
  const [pastEvents, setPastEvents] = useState([]);
  const today = new Date();

  useEffect(() => {
    axios
      .get("http://localhost:8000/api/events")
      .then((res) => setEvents(res.data))
      .catch((err) => console.log(err));
  }, [events]);

  useEffect(() => {
    events.length &&
      setNextEvents(events.filter((event) => new Date(event.date) > today));
    events.length &&
      setPastEvents(events.filter((event) => new Date(event.date) < today));
  }, [events]);

  return (
    <div className="w-full bg-500-red">
      <h1 className="bg-yellow-300">Nos évènements</h1>
      <h2 className="bg-green-300">A venir</h2>
      <section className="bg-pink-300">
        {nextEvents.length ? (
          nextEvents.map((event, index) => (
            <Event
              key={index}
              idEvent={event.id}
              title={event.title}
              description={event.description}
              date={event.date}
              place={event.place}
              setShowModal={setShowModal}
            />
          ))
        ) : (
          <p className="bg-blue-300">Aucun évènements à venir</p>
        )}
      </section>
      {cookies.monCookie && (
        <div>
          <h2>Passés</h2>
          <section className="events__container">
            {pastEvents.length ? (
              pastEvents.map((event, index) => (
                <Event
                  key={index}
                  idEvent={event.id}
                  title={event.title}
                  description={event.description}
                  date={event.date}
                  place={event.place}
                  setShowModal={setShowModal}
                />
              ))
            ) : (
              <p>Aucun évènements passés</p>
            )}
          </section>
          <section>
            <button id="addEvent" onClick={() => setShowModalCreateEvent(true)}>
              Ajouter un évènement
            </button>
            {showModalCreateEvent && (
              <ModalCreacteEvent
                setShowModalCreateEvent={setShowModalCreateEvent}
                showModalCreateEvent={showModalCreateEvent}
              />
            )}
            {showModalUpdateEvent && <ModalUpdateEvent />}
          </section>
        </div>
      )}
    </div>
  );
};

export default Events;
